.dashboard {
  background-color: #000000;

  .dashboard-header {
    margin-top: 24px;

    h1 {
      color: #ffffff;
      font-size: 20px;
      font-weight: 900;
      text-align: center;
    }
  }

  .dashboard-section {
    margin-top: 48px;
    display: flex;
    justify-content: space-around;

    .dashboard-sidenav {
      .sidenav {
        width: 200px;

        &.list-group {
          margin-right: 0;
        }

        .sidenav-item {
          display: flex;
          background-color: #111111;
          // padding: 15px 12px;

          .sidenav-link {
            font-size: 12px;
            // font-weight: 300;
            margin-left: 20px;
            color: #9c9c9c;
            padding-top: 8px;
            letter-spacing: 0.2px;

            .sidenav-icon {
              width: 32px;
              height: 32px;
            }
          }
        }
      }
    }

    .dashboard-contents {
      padding-top: 300px;
      display: flex;
      justify-content: center;

      .card {
        justify-content: center;
        border-radius: 5px;
        background-image: radial-gradient(
          circle at 18% -2%,
          #333333,
          #222222 33%,
          #1a1a1a 76%
        );

        .card-item {
          width: 239px;
          height: 239px;

          .card-link {
            margin-top: 74px;
            border: 0;
            font-size: 20px;
            width: 180px;
            height: 48px;
            text-align: center;
            border-radius: 24px;
            background-color: #171717;
            color: #ffffff;
          }
        }

        &:hover {
          background-image: radial-gradient(
            circle at 50% 0%,
            #444444,
            #333333 50%,
            #222222 89%
          );

          .card-link {
            background-color: #222222;
          }
        }
      }
    }

    .dashboard-offbar {
      // width: 200px;

      .auth {
        line-height: 3;
        display: flex;
        /* flex-direction: revert; */
        justify-content: space-between;
        padding: 0;
        width: 169px;
        height: 50px;
        border-radius: 30px;
        background-color: #222222;
        color: #ffffff;

        .auth-icon {
          position: relative;
          width: 50px;
          height: 50px;
          border-radius: 28px;
          background-color: #444444;

          img {
            position: absolute;
            transform: translate(50%, 50%);
          }
        }

        .auth-signout {
          position: relative;
          width: 32px;
          height: 32px;
          border-radius: 28px;
          background-color: #444444;
          margin: 9px;

          img {
            position: absolute;
            transform: translate(22%, 22%);
          }
        }
      }

      .offbar {
        margin-top: 530px;
        width: 200px;

        .offbar-item {
          margin-bottom: 20px;
          border-radius: 30px;
          background-color: #222222;
          color: #ffffff;
          cursor: pointer;

          .offbar-icon {
            width: 24px;
            height: 24px;
          }

          .offbar-link {
            color: #ffffff;

            font-size: 16px;
            margin-left: 12px;
          }

          &:hover {
            border: solid 1px #646464;
            background-color: #333333;
          }
        }
      }
    }
  }
}
